import React from 'react';

const UserManagement = () => {
  return (
    <div className="user-management">
      <h2>User Management</h2>
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Email</th>
            <th>Role</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {/* User data will be populated here */}
        </tbody>
      </table>
    </div>
  );
};

export default UserManagement;