import React, { useEffect } from 'react';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate
} from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';

// Auth Components
import Login from './components/auth/Login';
import Register from './components/auth/Register';
import PrivateRoute from './components/routing/PrivateRoute';

// Layout Components
import Dashboard from './components/dashboard/Dashboard';
import Navbar from './components/layout/Navbar';
import Sidebar from './components/layout/Sidebar';
import Footer from './components/layout/Footer';

// Admin Components
import UserManagement from './components/admin/UserManagement';
import SystemSettings from './components/admin/SystemSettings';

// Teacher Components
import CourseManagement from './components/teacher/CourseManagement';
import AssignmentCreation from './components/teacher/AssignmentCreation';
import GradeManagement from './components/teacher/GradeManagement';
import QuizCreation from './components/teacher/QuizCreation';

// Student Components
import MyCourses from './components/student/MyCourses';
import Assignments from './components/student/Assignments';
import Quizzes from './components/student/Quizzes';
import Grades from './components/student/Grades';

// Parent Components
import ChildrenProgress from './components/parent/ChildrenProgress';
import TeacherCommunication from './components/parent/TeacherCommunication';

// Shared Components
import Profile from './components/shared/Profile';
import Calendar from './components/shared/Calendar';
import Messages from './components/shared/Messages';
import Announcements from './components/shared/Announcements';

// Context
import { AuthProvider } from './context/auth/AuthState';
import { AlertProvider } from './context/alert/AlertState';

// Theme
import theme from './theme';

// Styles
import './App.css';

const App = () => {
  const { i18n } = useTranslation();

  useEffect(() => {
    // Check for saved language preference
    const savedLanguage = localStorage.getItem('language') || 'en';
    i18n.changeLanguage(savedLanguage);
  }, [i18n]);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AuthProvider>
        <AlertProvider>
          <Router>
            <div className="app">
              <Navbar />
              <div className="container">
                <Sidebar />
                <main className="main-content">
                  <Routes>
                    <Route path="/login" element={<Login />} />
                    <Route path="/register" element={<Register />} />
                    
                    {/* Dashboard */}
                    <Route 
                      path="/dashboard" 
                      element={
                        <PrivateRoute>
                          <Dashboard />
                        </PrivateRoute>
                      } 
                    />
                    
                    {/* Admin Routes */}
                    <Route 
                      path="/admin/users" 
                      element={
                        <PrivateRoute roles={['admin']}>
                          <UserManagement />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/admin/settings" 
                      element={
                        <PrivateRoute roles={['admin']}>
                          <SystemSettings />
                        </PrivateRoute>
                      } 
                    />
                    
                    {/* Teacher Routes */}
                    <Route 
                      path="/teacher/courses" 
                      element={
                        <PrivateRoute roles={['teacher']}>
                          <CourseManagement />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/teacher/assignments" 
                      element={
                        <PrivateRoute roles={['teacher']}>
                          <AssignmentCreation />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/teacher/grades" 
                      element={
                        <PrivateRoute roles={['teacher']}>
                          <GradeManagement />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/teacher/quizzes" 
                      element={
                        <PrivateRoute roles={['teacher']}>
                          <QuizCreation />
                        </PrivateRoute>
                      } 
                    />
                    
                    {/* Student Routes */}
                    <Route 
                      path="/student/courses" 
                      element={
                        <PrivateRoute roles={['student']}>
                          <MyCourses />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/student/assignments" 
                      element={
                        <PrivateRoute roles={['student']}>
                          <Assignments />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/student/quizzes" 
                      element={
                        <PrivateRoute roles={['student']}>
                          <Quizzes />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/student/grades" 
                      element={
                        <PrivateRoute roles={['student']}>
                          <Grades />
                        </PrivateRoute>
                      } 
                    />
                    
                    {/* Parent Routes */}
                    <Route 
                      path="/parent/progress" 
                      element={
                        <PrivateRoute roles={['parent']}>
                          <ChildrenProgress />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/parent/communication" 
                      element={
                        <PrivateRoute roles={['parent']}>
                          <TeacherCommunication />
                        </PrivateRoute>
                      } 
                    />
                    
                    {/* Shared Routes */}
                    <Route 
                      path="/profile" 
                      element={
                        <PrivateRoute>
                          <Profile />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/calendar" 
                      element={
                        <PrivateRoute>
                          <Calendar />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/messages" 
                      element={
                        <PrivateRoute>
                          <Messages />
                        </PrivateRoute>
                      } 
                    />
                    <Route 
                      path="/announcements" 
                      element={
                        <PrivateRoute>
                          <Announcements />
                        </PrivateRoute>
                      } 
                    />
                    
                    {/* Default Route */}
                    <Route 
  path="/" 
  element={
    <PrivateRoute>
      <Navigate to="/dashboard" replace />
    </PrivateRoute>
  } 
/>
                  </Routes>
                </main>
              </div>
              <Footer />
            </div>
          </Router>
        </AlertProvider>
      </AuthProvider>
    </ThemeProvider>
  );
};

export default App;